.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-container{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}
.nav-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo{
  color:#61dafb;
}
.nav-toggle{
  cursor: pointer;
  background: none;
  border: none;
  font-size: 24px;
  color: #61dafb;
  position: absolute;
  right: 20px;
}
ul{
  display: flex;
  justify-content: space-between;
}
li{
  list-style: none;
  padding: 0 10px;
}
a{
  text-decoration: none;
}
.links-contaner a{
  color: gray;
  font-weight: 600;
  transition: all .5s;
}
.links-contaner a:hover{color: #00ccff;}

.social-icons{font-size: 24px;}

/* media */
@media screen and (min-width: 800px){
  .nav-toggle{
    display: none;
  }
}
@media screen and (max-width: 800px){
  .nav-container{
    flex-direction: column;
    align-items: flex-start;
  }
  .nav-header{
    margin: 0 50px;
  }
  .links{flex-direction: column;}
  li{padding: 10px;}
  .links-contaner a:hover{
    padding-left: 10px;
  }
  .social-icons{
    margin: 0 auto;
  }
}
